.task-details-checklists {
   grid-column: 1/3;

   .task-checklist {
      width: 100%;

      .task-details-sublist {
         width: 100%;
         margin-left: 0.7rem;
         .strikethrough {
            text-decoration: line-through;
         }
      }
   }

   .add-item-btn {
      margin-inline-start: 2.3rem;
      color: $primaryColor;
      background-color: #e4e6ea;
      &:hover {
         background-color: darken($color: #e4e6ea, $amount: 3);
      }
   }
}

.sublist-progress-bar {
   width: 90%;
   height: 20px;
   border-radius: rem(4px);
   margin-left: 1.1rem;
   background-color: #ddd;

   
}
.progress-bar-foreground {
   height: 100%;
   transition: width 0.5s ease;
}


.task-details-sublist {
   border-radius: 0.2rem;
   background-color: inherit;
   padding: 0.6rem;
   margin-bottom: 0.6rem;

   .checklist-progress {
      display: flex;
      align-items: center;
      justify-self: center;
      gap: 3px;
   }

   .sublist-header {
      display: flex;
      justify-content: space-between;
      align-items: center;

      h3 {
         color: $primaryColor;
         display: flex;
         align-items: center;
         font-size: rem(16px);
         font-weight: 600;

         svg {
            margin-right: 1.2rem;
            margin-left: 0.25rem;
         }
      }

      button {
         padding: 0.5rem;
         border-radius: 0.3rem;
         border: none;
         background-color: #e4e6ea;
         color: #5e6c84;
         cursor: pointer;

         &:hover {
            background-color: darken($color: #e4e6ea, $amount: 3);
         }
      }
   }

   .progress-percent {
      text-align: center;
      font-size: rem(11px);
      padding-left: 0.3rem;
      max-width: 20px;
      color: $primaryColor;
   }

   .sublist-progress-bar {
      height: 0.5rem;
      background-color: #ddd;

      .progress-bar-foreground {
         height: 100%;
         border-radius: 0.1rem;
      }
   }

   .sublist-todos {
      div {
         display: flex;
         align-items: center;
         margin-bottom: 0.4rem;
         max-height: 22px;
         gap: 0.1rem;

         input[type='checkbox'] {
            margin-right: 1.2rem;
            margin-left: 0.3rem;
            box-shadow: inset 0 0 0 1px #dfe1e6;
            border: none;
            cursor: pointer;
            &:active,
            &:focus {
               box-shadow: inset 0 0 0 2px #dfe1e6;
               border: none;
            }

            &:checked {
               box-shadow: none;
            }
         }

         button {
            margin-left: auto;
            padding: 0.3rem;
            border: none;
            background-color: transparent;
            color: #5e6c84;
            cursor: pointer;
         }
      }

      span {
         font-size: rem(14px);
         color: $primaryColor;
      }
   }

   form {
      display: flex;
      margin-top: 0.6rem;

      input[type='text'] {
         flex-grow: 1;
         margin-right: 0.6rem;
      }

      button {
         padding: 0.3rem 0.6rem;
         border: none;
         background-color: #5aac44;
         color: #fff;
         cursor: pointer;

         &:last-child {
            background-color: #ea2525;
         }
      }
   }
}

.add-new-item-container {
   display: flex;
   flex-direction: column;

   input {
      width: 95%;
      margin-left: 3.5rem;
      height: 56px;
      align-self: center;
      padding-bottom: 1.5rem;
      padding-left: 0.5rem;
      border-radius: 0.1rem;
      border: 2px solid #388bff;

      &:focus {
         border: none;
         outline-color: #388bff;
      }
   }

   div {
      button:first-child {
         background-color: $clr1;
         padding: 0.6rem;
         border-radius: 0.3rem;

         &:hover {
            background-color: darken($color: $clr1, $amount: 5);
         }
      }

      button:last-child {
         background-color: transparent;
         color: $primaryColor;

         &:hover {
            background-color: darken($color: #e4e6ea, $amount: 1);
         }
      }

      padding: 0 0.9rem;
      margin: 0.2rem 0;
      margin-left: 1rem;
   }
}

.checklist-content {
   label {
      font-weight: 700;
      font-size: rem(12px);
      margin-left: 0.3rem;
   }

   input {
      border: none;
      border-radius: 3px;
      box-shadow: inset 0 0 0 2px var(--ds-border-input, #dfe1e6);
      box-sizing: border-box;
      display: block;
      line-height: 20px;
      margin-bottom: 12px;
      margin-left: 0.3rem;
      outline: none;
      padding: 8px 12px;
      transition-duration: 85ms;
      transition-property: background-color, border-color, box-shadow;
      transition-timing-function: ease;

      &:focus {
         box-shadow: inset 0 0 0 2px #388bff;
      }
   }

   button {
      background-color: $clr1;
      color: #fff;
      width: rem(73px);
      height: rem(32px);
      padding-left: rem(24px);
      padding-right: rem(24px);
      border-radius: rem(3px);

      &:hover {
         background-color: darken($color: $clr1, $amount: 5);
      }
   }
}
