.labels-content {
   .labels-list {
      display: flex;
      flex-direction: column;
      gap: 4px;
   }
   h4 {
      font-size: 12px;
      font-weight: 600;
      line-height: 16px;
      margin-top: 16px;
      color: #44546f;
      text-align: left;
      margin-bottom: 15px;
   }
}
.modal-labels-search {
   .modal-labels-input {
      width: 100%;
      border: none;
      border-radius: 3px;
      box-shadow: inset 0 0 0 2px #dfe1e6;
      display: block;
      overflow-x: hidden;
      overflow-y: auto;
      padding: 10px;

      &:hover {
         background-color: #f7f8f9;
      }
      &:focus {
         box-shadow: inset 0 0 0 2px #388bff;
         outline: none;
         border: none;
      }
      &::placeholder {
         color: #43546f;
      }
   }
}

.label-container {
   display: flex;
   align-items: center;

   .label-block {
      // padding: 0 0 4px 4px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
         'Noto Sans', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', sans-serif;
      font-weight: 500;
      font-size: 14px;
      padding-inline: 12px;
      height: 32px;
      border-radius: 3px;
      width: 100%;
      line-height: 32px;
      margin-left: 5px;

      &:hover {
         cursor: pointer;
      }
   }

   .label-checkbox {
      box-shadow: inset 0 0 0 2px #dfe1e6;
      border: none;
      cursor: pointer;
      &:active,
      &:focus {
         box-shadow: inset 0 0 0 2px #dfe1e6;
         border: none;
      }

      &:checked {
         box-shadow: none;
      }
   }

