.edit-label-content {
   hr {
      background-color: #091e4224;
      height: 1px;
      margin: 16px 0;
      padding: 0;
      width: 100%;
   }
   h4 {
      font-size: 12px;
      font-weight: 600;
      line-height: 16px;

      color: #44546f;
      text-align: left;
   }

   .preview-container {
      margin: 0 -12px;
      background-color: #f7f8f9;
      padding: 32px;

      .preview-label {
         // padding: 0 0 4px 4px;
         font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
            'Noto Sans', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', sans-serif;
         font-weight: 500;
         font-size: 14px;
         padding-inline: 12px;
         height: 32px;
         border-radius: 3px;
         width: 100%;
         line-height: 32px;
         margin-left: 5px;

         &:hover {
            cursor: pointer;
         }
      }
   }

   .edit-label-controls {
      display: flex;
      justify-content: space-between;
      .save-button {
         width: 50px;
         height: 28px;
         background-color: #0c66e4;
         color: white;
         border: none;
         border-radius: 4px;

         &:hover {
            background-color: #0055cc;
         }
      }

      .delete-button {
         width: 60px;
         height: 28px;
         background-color: #ca3521;
         color: white;
         border: none;
         border-radius: 4px;
         &:hover {
            background-color: #ae2a19;
         }
      }
   }

   .edit-label-search {
      .edit-label-input {
         width: 100%;
         border: none;
         border-radius: 3px;
         box-shadow: inset 0 0 0 2px #dfe1e6;
         display: block;
         overflow-x: hidden;
         overflow-y: auto;
         padding: 10px;

         &:hover {
            background-color: #f7f8f9;
         }
         &:focus {
            box-shadow: inset 0 0 0 2px #388bff;
            border: none;
            outline: none;
         }
         &::placeholder {
            color: #43546f;
         }
      }
   }
}
